#Task {
  display: grid;
  grid-template-columns: 1fr 0.15fr;
  margin-bottom: 10px;
}
#Title {
  margin-right: 15px;
  text-align: left;
}
#Delete {
  padding: 10px;
  margin-right: 10px;
  border-radius: 5px;
  border: transparent;
  cursor: pointer;
  font-family: inherit;
  background-color: #dc4c64;
  box-shadow: 0 4px 9px -4px #dc4c64;
  color: white;
  text-transform: uppercase;
}

#Header {
  font-family: inherit;
  font-weight: bold;
  line-height: 1.2;
  font-size: calc(1.275rem+0.3vw);
}

#TodoList {
  padding: 5vh;
}
#TextBox {
  font-family: inherit;
  margin-right: 10px;
  width: 50%;
  padding: 10px;
  border-radius: 5px;
  border-color: #cdcdcd;
}
#AddTodo {
  font-family: inherit;
  padding: 10px;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
  border: transparent;
  background-color: #3b71ca;
  box-shadow: 0 4px 9px -4px #3b71ca;
  color: white;
  text-transform: uppercase;
}

